import {
  FooterBottom,
  FooterCntJp,
  FooterCntEn,
} from "~/components/atoms/footer";

export default {
  components: {
    FooterBottom,
    FooterCntJp,
    FooterCntEn,
  },
  mounted() {
    const scene1 = this.$scrollmagic
      .scene({
        triggerElement: "#footer-trigger",
        triggerHook: 1,
        reverse: true,
      })
      .setTween("#footer-content", {
        css: {
          opacity: "1",
          transform: "translateX(0px)",
        },
      });
    this.$scrollmagic.addScene(scene1);
  },
};
